import { useState } from "react"
import { nGonState } from "./Polygon.types"
import { XY, ngon, translate, uniform, Cubic, multiply, add } from "../../geometry";

export const useNgon = (sides: number, scale:number, rotation: number, cornerRadius: number, strokeWidth: number) => {
	const [state, setState] = useState<nGonState>({sides,scale,rotation,cornerRadius,strokeWidth});
	const radius = state.scale/2;
	const [cpts, vb] = ngon(sides, radius, rotation, cornerRadius, [0, 0]);
	const hsw = strokeWidth/2;
	const nvb = add(multiply(vb, -1), hsw);
	const points = translate(cpts, [nvb[0], nvb[1], nvb[0], nvb[1]]);
	const viewBox: Cubic = [0, 0, vb[2] * 2, vb[3] * 2];
	return [points, viewBox];
}